
@charset "UTF-8";
html {
  font-size: 10vw;
  font-family: "微软雅黑";
}
* {
  margin: 0px;
  padding: 0px;
  font-size: 0.5rem;
}
img {
  max-width: 100%;
}
input, button, textarea {
  border: none;
  outline: none;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.wp100 {
  width: 100%;
}
.hp100 {
  height: 100%;
}
.fl {
  float: left;
}
.fr {
  float: right;
}



